round-size:cover;width:50px;height:50px;position:absolute;animation:timer 4s ease-out;transition:4s all;z-index:1;top:-10%}@keyframes timer{0%{top:-10%;transform:rotateY(0deg)}100%{top:110%;transform:rotateY(720deg)}}.better-bullets{background-image:url("../img/bonuses/weapone.png");background-size:cover;width:50px;height:50px;position:absolute;animation:bullets 4s ease-out;transition:4s all;z-index:1;top:-10%}@keyframes bullets{0%{top:-10%;transform:rotateY(0deg)}100%{top:110%;transform:rotateY(720deg)}}.hide{display:none}.main-menu{border:1px solid black;display:flex;align-items:center;justify-content:left;position:relative}.main-menu video{width:100vw;height:auto}.main-menu ul{position:absolute}.main-menu ul li{list-style:none;padding:30px 0 0 50px}.main-menu ul li button{width:200px;height:70px}button{background:none;border:0;box-sizing:border-box;margin:1em;padding:1em 2em;box-shadow:inset 0 0 0 2px #fff;color:#fff;font-size:inherit;font-weight:700;position:relative;vertical-align:middle}button::before,button::after{box-sizing:inherit;content:'';position:absolute;width:100%;height:100%}.draw{transition:color 0.25s}.draw::before,.draw::after{border:2px solid transparent;width:0;height:0}.draw::before{top:0;left:0}.draw::after{bottom:0;right:0}.draw:hover{color:#60daaa}.draw:hover::before,.draw:hover::after{width:100%;height:100%}.draw:hover::before{border-top-color:#60daaa;border-right-color:#60daaa;transition:width 0.25s ease-out, height 0.25s ease-out 0.25s}.draw:hover::after{border-bottom-color:#60daaa;border-left-color:#60daaa;transition:border-color 0s ease-out 0.5s, width 0.25s ease-out 0.5s, height 0.25s ease-out 0.75s}.table{position:absolute;text-align:center;color:#fff;border:2px solid white;padding:10px;margin-left:100px}.table button{width:100%;position:absolute}td,tr{padding:10px}.copyright{width:300px;height:auto;text-align:justify;position:absolute;color:white;font-weight:700;margin-left:100px}.copyright button{margin-left:80px}
